<template>
    <div class="uk-animation-slide-bottom-medium">  
        <div class="uk-width-2-3 uk-align-center uk-align-center uk-margin-remove-top">  

            <!-- LOGO -->
            <div v-if="data.logoSrc">
                <div class="uk-flex uk-flex-center">
                    <div class="uk-width-1-3 uk-padding-small">
                        <img :src="data.logoSrc">
                    </div>
                </div>
            </div>
            
            <div v-if="data.finished == false">
                <div class="uk-alert-warning" uk-alert>
                    <a class="uk-alert-close" uk-close></a>
                    Page is under construction
                </div>
            </div>

            <!-- CONTENT BROWSER -->    
            <div class="uk-grid uk-grid-small" uk-grid uk-height-match="target: > div > .uk-card">

                <div class="uk-width-expand">
                    <div class="uk-card uk-card-small uk-card-default">
                        <div uk-slideshow="animation: slide; ratio: 16:9">
                            <div class="uk-position-relative uk-visible-toggle outlined" tabindex="-1">
                                <ul class="uk-slideshow-items uk-background-secondary" uk-lightbox>
                                    <li v-for="(content, contentIndex) in data.content" :key="contentIndex">
                                        <a v-if="content.type == 'image'" :href="content.src">
                                            <img 
                                                class="uk-align-center" 
                                                :src="content.src" 
                                                style="height:100% !important">
                                        </a>
                                        <video 
                                            v-if="content.type == 'video'" 
                                            :src="content.src" 
                                            uk-video 
                                            uk-cover 
                                            controls>
                                        </video>
                                        <iframe 
                                            v-if="content.type == 'iframe' || content.type == 'youtube'"  
                                            :src="content.src" 
                                            style="width:100%; height:100%;" 
                                            allowfullscreen
                                            scrolling="no">
                                        </iframe>
                                    </li>
                                </ul>

                                <a class="uk-position-center-left uk-position-small uk-hidden-hover" uk-slidenav-previous uk-slideshow-item="previous"></a>
                                <a class="uk-position-center-right uk-position-small uk-hidden-hover" uk-slidenav-next uk-slideshow-item="next"></a>
                            </div>
                        
                            <!-- PREVIEWS -->
                            <div v-if="data.content.length > 1">
                                <div class="uk-margin-small-top">
                                    <div class="uk-grid-collapse" uk-grid uk-slider>

                                        <!-- <div class="uk-width-auto">
                                            <a class="uk-button uk-button-secondary" uk-slidenav-previous uk-slideshow-item="previous"></a>
                                        </div> -->

                                        <div class="uk-width-expand">
                                            <ul class="uk-slider-items">
                                                <li class="outlined"
                                                    v-for="(preview, previewIndex) in data.content" 
                                                    :key="previewIndex">
                                                    <a :uk-slideshow-item="previewIndex">
                                                        <div class="uk-inline uk-dark">
                                                            <img :src="preview.preview" class="uk-preserve-width" width="120">
                                                        </div>
                                                    </a>
                                                    
                                                    <div v-if="preview.type == 'youtube'">
                                                        <div class="uk-position-center uk-panel">
                                                            <a
                                                                :uk-slideshow-item="previewIndex"
                                                                class="uk-icon-button" 
                                                                uk-icon="icon: youtube;">
                                                            </a>
                                                        </div>
                                                    </div>
                                                </li>
                                            </ul>
                                        </div>

                                        <!-- <div class="uk-width-auto">
                                            <a class="uk-button uk-button-secondary" uk-slidenav-next uk-slideshow-item="next"></a>
                                        </div> -->

                                    </div>      
                                </div>
                                <div v-if="data.content.length > 8" class="uk-flex uk-flex-center">
                                    <span class="uk-text-muted">drag previews to scroll through</span>
                                </div>
                            </div> 
                        </div>
                    </div>
                </div>

                <!-- SIDEBAR INFO -->
                <div class="uk-width-1-5">
                    <div class="uk-card uk-card-small uk-card-default">
                        <div v-if="data.sideImgSrc != ''">
                            <img :src="data.sideImgSrc">
                        </div>

                        <div class="uk-text-small uk-padding-small">
                            <div class="uk-grid-collapse uk-child-width-1-1" uk-grid>
                                <div v-if="data.title" class="uk-grid-small" uk-grid uk-tooltip="Project title">
                                    <div class="uk-width-auto"><span uk-icon="info"></span></div>
                                    <div class="uk-width-expand uk-text-bold uk-text-break">
                                        <span v-html="data.title"></span>
                                    </div>
                                </div>
                                <div v-if="data.tools" class="uk-grid-small" uk-grid uk-tooltip="Applications used">
                                    <div class="uk-width-auto"><span uk-icon="database"></span></div>
                                    <div class="uk-width-expand uk-text-break">
                                         <span v-html="data.tools"></span>
                                    </div>
                                </div>
                                <div v-if="data.team" class="uk-grid-small" uk-grid uk-tooltip="Team">
                                    <div class="uk-width-auto"><span uk-icon="users"></span></div>
                                    <div class="uk-width-expand uk-text-break">
                                        <span v-html="data.team"></span>
                                    </div>
                                </div>
                                <div v-if="data.role" class="uk-grid-small" uk-grid uk-tooltip="My role">
                                    <div class="uk-width-auto"><span uk-icon="user"></span></div>
                                    <div class="uk-width-expand uk-text-break">
                                        <span v-html="data.role"></span>
                                    </div>
                                </div>
                                <div v-if="data.category" class="uk-grid-small" uk-grid uk-tooltip="Category">
                                    <div class="uk-width-auto"><span uk-icon="folder"></span></div>
                                    <div class="uk-width-expand uk-text-break">
                                        <span v-html="data.category"></span>
                                    </div>
                                </div>
                                <div v-if="data.duration" class="uk-grid-small" uk-grid uk-tooltip="Project duration">
                                    <div class="uk-width-auto"><span uk-icon="clock"></span></div>
                                    <div class="uk-width-expand uk-text-break">
                                        <span v-html="data.duration"></span>
                                    </div>
                                </div>
                                <div v-if="data.year" class="uk-grid-small" uk-grid uk-tooltip="Date">
                                    <div class="uk-width-auto"><span uk-icon="calendar"></span></div>
                                    <div class="uk-width-expand uk-text-break">
                                        <span v-html="data.year"></span>
                                    </div>
                                </div>
                            </div>
                            
                            <hr>

                            <div v-if="data.shortDescription" class="uk-margin uk-margin-right">
                                <span class="uk-text-small uk-text-justify">
                                    {{ data.shortDescription }}
                                </span>
                            </div>

                            <hr>

                            <div 
                                v-if="data.tags" 
                                class="uk-margin uk-margin-right uk-margin-bottom">
                                <div class="uk-grid-collapse uk-width-1-1" uk-grid>
                                    <div 
                                        v-for="(tag, index) in data.tags" 
                                        :key="index" 
                                        class="uk-margin-remove">
                                        <span class="uk-label uk-margin-small-right">
                                            {{ tag }}
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div v-if="data.links" class="uk-margin">
                <div class="uk-grid uk-grid-small uk-flex uk-flex-center" uk-grid>
                    <div 
                        v-for="(link, index) in data.links"
                        :key="index">
                        <a class="uk-button uk-button-default uk-button-small" :href="link.url" target="blank">
                            <span v-if="link.icon != ''" :uk-icon="link.icon"></span>&nbsp;
                            <span class="uk-text-middle">{{ link.name }}</span>
                        </a>
                    </div>
                </div>
            </div>

            <hr>

            <!-- ACHIEVEMENTS -->    
            <div v-if="data.achievements.awards" class="uk-margin">
                <div class="uk-card uk-card-small uk-card-default uk-card-body">
                    <div class="uk-flex uk-flex-center">
                        <div class="uk-grid-medium uk-grid-divider" uk-grid>
                            <div v-if="data.achievements.awards" class="uk-width-auto">
                                <div class="uk-grid-collapse" uk-grid>
                                    <div 
                                        v-for="(award, index) in data.achievements.awards" 
                                        :key="index" 
                                        :uk-tooltip="award.text">
                                        <img src="assets/images/awards/leaf_L.png" width="25">
                                        <img :src="award.img" width="70">
                                        <img src="assets/images/awards/leaf_R.png" width="25">
                                    </div>
                                </div>
                            </div>

                            <div v-if="data.achievements.nominations" class="uk-width-expand">
                                <div class="uk-grid-collapse" uk-grid>
                                    <div 
                                        v-for="(nomination, index) in data.achievements.nominations" 
                                        :key="index"
                                        :uk-tooltip="nomination.text">
                                        <img :src="nomination.img" width="50" >
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <!-- DESCRIPTION -->
            <div v-if="data.description" class="uk-margin">
                <div class="uk-card uk-card-small uk-card-default uk-card-body" :class="[ data.dark ? 'uk-card-secondary' : 'uk-card-default' ]">
                    <img src="assets/head.gif" class="uk-align-left" width="60">
                    <div class="uk-text-justify">
                        <span v-html="data.description"></span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Data from '@/assets/data/portfolio.json'

export default {
    data(){
        return {}
    },

    computed: {
        data: function () {
            for (let i = 0; i < Data.portfolioData.length; i++) {
                if (Data.portfolioData[i].id == this.$route.params.id) {
                    return Data.portfolioData[i];
                }
            }
        }
    }
}
</script>

<style scoped>

    .outlined {
        border: 1px solid black;
    }

</style>